nav{
    background: rgba(233, 228, 228, 0.2);
    width: max-content;
    display: block;
    padding: 0.3rem 1rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
   
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: rgba(56, 49, 49, 0.6);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0);
}

nav a:active{
    background: var(--color-bg);
    color: var(--color-black);
}


