@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@700&family=Jost:wght@400;500;700&family=Poppins:wght@300;400;500;600&display=swap');

:root{
    --primary-color: hsl(0, 0%, 100%);

    --title-color: rgb(0,0,0);

    --text-color: rgb(0,0,0);

    --bg-color: hsl(0, 0%, 100%);

    --bg-color-alt: #007BFF;

    --container-color: hsl(252, 4%, 78%);

    --border-color: hsl(240, 10%, 84%); 
    --first-gradient: linear-gradient(0deg,
    var(--bg-color-alt) 0%,
    var(--bg-color) 100%
);
--second-gradient: linear-gradient(180deg,
    var(--bg-color-alt) 0%,
    var(--bg-color) 100%
);
--third-gradient: linear-gradient(180deg,
    var(--bg-color-alt) 0%,
    var(--bg-color) 100%
);

--body-font:'Jost', sans-serif;
--second-font:'Caveat', cursive;

--biggest-font-size:90px;
--h1-font-size:44px;
--h2-font-size:30px;
--h3-font-size:28px;
--h4-font-size:24px;
--h5-font-size:22px;
--largest-font-size:21px;
--larger-font-size:20px;
--large-font-size:18px;
--normal-font-size:16px;
--small-font-size:15px;
--smaller-font-size:14px;
--tiny-font-size:13px;

--shadow:5px 5px rgb(255 255 255 /10%);

--transition:cubic-bezier(o.3,0,0.3,1);
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

button,
input,
body{
    font-family: var(--body-font);
    font-size:var(--normal-font-size);

}

body{
    color: var(--text-color);
    background-color: var(--bg-color);
   
}
h1,
h2,
h3{
    color: var(--title-color);
    font-weight:700;
}
ul{
    list-style: none;
}
p{
    line-height: 1.7;
}

a{
    text-decoration: none;
}
input,
textarea,
button{
    border: none;
    outline: none;
    background: transparent;
}

button{
    cursor: pointer;
}
img{
    max-width: 100%;
}

.container{
    background-color: var(--primary-color);
    max-width: 1300px;
    padding-inline: 12px;
    margin-inline: auto;
}